<script setup lang="ts">
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type { IDataTypeInstance } from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesIntegerValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesIntegerValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesIntegerValueConfiguration>({
  set: ({ max, min, ...rest }) =>
    ({
      max: Number(max),
      min: Number(min),
      ...rest,
    }) as IDataTypesIntegerValueConfiguration,
});
</script>

<template>
  <ACol>
    <ATextField
      v-model.number="configuration.min"
      :label="$t('stringValue.minLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.max"
      :label="$t('stringValue.maxLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>
